/* -- {{hotLIne}} --/
.bg-dark
	background-color: #f9f8f7

.section
	padding-top: 55px
	text-align: center
	+r(650)
		padding-top: 30px

	&.mod_first
		padding-top: 165px
		+r(1000)
			padding-top: 100px
		+r(650)
			padding-top: 90px
	&.mod_last
		padding-bottom: 80px
	&.mod_titleBig
		.title__article
			font-size: 70px
			+r(1000)
				font-size: 50px
			+r(650)
				font-size: 24px
	//todo
	//&.mod_last
	//	margin-bottom: 10px// where they are&
	//	padding-bottom: 30px// where they are&
	&.mod_pages
		+ .pagination
			margin: 30px auto 100px
			+r(650)
				margin: 20px auto 30px
	&.mod_short-pt
		padding-top: 120px
		+r(1000)
			padding-top: 80px

	&__inner
		max-width: 1280px
		width: calc(100% - 40px)
		margin: 0 auto
		font-size: 0
		+clr

	&__inside
		max-width: 1040px
		width: 100%
		margin: 0 auto
	.title__article
		font-size: 50px
		margin-bottom: 1em
		+r(1000)
			font-size: 24px

.text-left
	text-align: left
.hotLine
	display: flex
	flex-direction: column
	align-self: flex-start
	margin: 15px
	+r(1250)
		margin-top: 22px
	&__label
		color: $gray
		font-size: 14px
		line-height: .8
	&__tel
		font-size: 2.4rem
		color: #fff
		cursor: pointer
		white-space: nowrap
		+rb
		+r(1250)
			font-size: 2rem

.pagination
	width: 450px
	height: 57px
	margin: 55px auto 50px
	+clr
	+r(650)
		text-align: center
		width: 300px
	+r(450)
		width: 100%
	&__left,
	&__right
		position: relative
		width: 35px
		height: 57px
		float: left
		margin-right: 75px
		+r(650)
			margin: 0
		&:after
			content: ""
			position: absolute
			top: 50%
			left: 50%
			transform: matrix(1, 0, 0, 1, -7, -12)
			+s(icon-prev)
			+tr(.4s)
		&:hover:after
			transform: matrix(.8, 0, 0, .8, -7, -12)

	&__right
		float: right
		margin-right: 0
		&:after
			transform: matrix(1, 0, 0, 1, -7, -12)
			+s(icon-next)

	&__list
		display: inline-block
		font-size: 0
	&__item
		display: inline-block
		width: 32px
		text-align: center
		+tr(.4s)
		&.is_active
			background-color: #eee8e0
		&:hover
			background-color: #eee8e0
			opacity: .8
	&__link
		display: block
		line-height: 57px
		font-size: 16px
		color: #000
		+rr

.component
	&__slider
		position: relative
		width: 1204px
		// width: 96%
		margin: 0 auto
		padding-top: 55px
		+r(1400)
			width: calc(96% - 150px)
			padding: 75px 0 0
		+r(650)
			width: calc(100% - 20px)
			padding: 0
		.slick-track
			margin: 0 5px
			+r(650)
				margin: 0
		.slick-list
			margin-right: -30px
			padding: 0 10px 0 0// debag this if neads
			+r(650)
				padding: 0 10px
				margin: 0

.docs
	position: relative
	font-size: 0
	max-width: 1280px
	width: 94%
	margin: 0 auto
.doc
	width: calc(100% / 3 - 1px)
	display: inline-block
	vertical-align: top
	margin-right: 1px
	margin-bottom: 20px
	&.shadow-hover
		+r(1000)
			width: calc(100% / 2 - 1px)
		+r(650)
			width: calc(100%)
			margin-right: 0
		.doc__pic
			+r(650)
				height: 220px
	&__title
		text-align: left
		font-size: 18px
		color: $gold
		+rr
		+limitLines($maxLinesPortrait: 1, $maxLinesLandscape: 1, $fgCol: #f4f0eb, $bgCol: #f4f0eb, $lineHeight: 1.8 )
	&__pic
		display: block
		height: 290px
		position: relative
		width: 100%
		overflow: hidden
		&:before
			content: ''
			position: absolute
			top: 50%
			bottom: 50%
			left: 50%
			right: 50%
			background-color: rgba(0, 0, 0, .4)
			z-index: -10
			+tr(.5s)
		&:after
			content: ''
			position: absolute
			top: 50%
			left: 50%
			transform: scale(.2) translate(-50%, -50%)
			width: 51px
			height: 51px
			background-image: url(../img/icons/lupa.png)
			z-index: -10
			+tr(.4s)
	&__img
		+vcenter
		min-height: 100%
		min-width: 100%
	&__content
		width: 100%
		padding: 30px 35px 30px
		background-color: #f4f0eb
	&__table
		width: 100%
		margin: 0 0 10px
		text-align: left
		td
			vertical-align: top
			padding-bottom: 15px
			font-size: 14px
			color: #000
			+rr
			&.doc__table_title
				width: 94px
				padding-right: 5px
				font-size: 14px
				+rm
	.btn
		background-color: $n
		width: 235px
		height: 50px
		line-height: 50px
		&:hover
			background-color: #FFF
			color: #000


//  --  404  --
.header-404
	display: flex
	flex-direction: column
	justify-content: center
	align-items: center
	height: 146px
	background-color: #FFF
	+r(850)
		height: 80px
.logo-404
	width: 287px
	font-size: 0
	+r(850)
		width: 224px
	img
		width: 100%
.main-404
	position: relative
	background-color: #f9f8f7
	padding: 78px 0 580px
	text-align: center
	+r(1000)
		padding-bottom: 60vw
	+r(850)
		padding-top: 30px
	&-wrap
		width: calc(100% - 40px)
		margin: 0 auto
		font-size: 0
.desc-404
	font-size: 18px
	letter-spacing: .025em
	color: #000
	margin-bottom: 0
	+rl
	+r(850)
		font-size: 14px

.pic-404
	vertical-align: top
	position: absolute
	left: 50%
	bottom: -47px
	+trx(-50%)
	max-width: calc(100% - 20px)
	+r(1000)
		bottom: -5vw

.footer-404
	background-color: #efebe9
	text-align: center
	padding: 78px 0 88px
	margin-bottom: 74px
	+r(850)
		margin-bottom: 0
		padding: 50px 0
	.btn
		background-color: $n
		&:hover
			background-color: $gold01

.pagePrev
	float: left
	display: inline-block
	position: relative
	padding-left: 40px
	margin-bottom: 40px
	font-size: 14px
	line-height: 30px
	color: $gold
	+rr
	+tr(.4s)
	+r(1000)
		margin-bottom: 20px
	&:before
		content: ''
		position: absolute
		left: 0
		top: 50%
		+try(-50%)
		background-image: url('../img/icons/icon-pagePrev.png')
		width: 6px
		height: 11px
		+tr(.4s)
	&:hover
		opacity: .8

.heading__time
	font-size: 14px
	color: #000
	text-transform: uppercase
	+rr
	+r(850)
		font-size: 13px

.shadow-hover
	position: relative
	+tr(.4s)
	&:hover
		box-shadow: 0 0 0 11px #fff, 0 0 32px 10px rgba(131, 109, 108, 0.4)
		z-index: 10
		.doc__pic
			cursor: pointer
			&:before
				content: ''
				top: 0
				bottom: 0
				left: 0
				right: 0
				z-index: 10
			&:after
				content: ''
				z-index: 20
				transform: scale(1) translate(-50%, -50%)

.promo
	@extend .doc
	&.shadow-hover
		+r(1000)
			width: calc(100% / 2 - 1px)
		+r(650)
			width: calc(100%)
			margin-right: 0
	&__pic
		display: block
		height: 290px
		position: relative
		width: 100%
		overflow: hidden
	&__img
		@extend .doc__img
	&__content
		@extend .doc__content
	&__title
		text-align: left
		font-size: 18px
		color: $gold
		+rr
		text-align: center

.variants
	counter-reset: list
	text-align: left
	margin: 50px 0 60px
	padding-bottom: 50px
	border-bottom: 1px solid #ccc
	+clr
	+r(800)
		margin: 30px 0
		padding-bottom: 30px

.variant
	position: relative
	float: left
	vertical-align: top
	width: calc(50% - 60px)
	padding-left: 80px
	text-align: left
	font-size: 18px
	line-height: 24px
	+rl
	+r(850)
		width: 100%
		margin-bottom: 60px
		padding-left: 65px

	&:before
		position: absolute
		top: 13px
		left: 20px
		counter-increment: list
		content: counter(list)' '
	&:after
		content: ''
		position: absolute
		left: 0
		top: 0
		width: 50px
		height: 50px
		border: 3px solid #e0d6c8
		border-radius: 100%
	&:nth-child(2n)
		float: right
		margin-bottom: 0
	&__or
		position: absolute
		top: 0
		left: -80px
		display: inline-block
		font-size: 18px
		color: #000
		+rl
		+r(800)
			left: 65px
			top: -40px
	&__link
		display: inline-block
		color: #754c24
		line-height: 1
		border-bottom: 1px solid #e0d6c8
		+tr(.4s)
		&:hover
			opacity: .7
			border: none
		&.border-dotted
			border-bottom-style: dotted

.liability
	font-size: 0
	margin-bottom: 50px
	text-align: left
	+clr
	+r(650)
		margin-bottom: 15px
	&__pic
		float: left
		display: inline-block
		vertical-align: top
		margin: 0 32px 20px 0
		+r(650)
			display: none
	&__text
		// float: left
		margin-bottom: 30px
		font-size: 18px
		line-height: 30px
		color: #000
		+rl
		+r(650)
			font-size: 14px
			line-height: 20px
			margin-bottom: 15px

.garant
	margin: 40px auto 80px
	max-width: 554px
	width: 100%
	+r(650)
		margin-bottom: 40px
	&__fieldset
		width: 100%
		position: relative
		text-align: left
		overflow: hidden
		&.mod_b20
			margin-bottom: 20px
		&.mod_b30
			margin-bottom: 30px
		&.input--filled
			.garant__label
				top: 3px
				font-size: 10px
	&__inp
		+placeholder
			color: #000
			font-size: 16px
			+rl
	&__inp,
	.select
		width: 100%
		height: 50px
		margin-bottom: 9px
		padding: 0 20px
		border: 1px solid #cfc4b6
		background-color: #FFF
		color: #000
		font-size: 16px
		text-align: left
		+rr
		+tr(.4s)
		+r(650)
			font-size: 14px
		&:focus
			box-shadow: 0 0 12px rgba(131,109,108,0.4)
			border: 1px solid #cfc4b6
			outline: none
			& + .garant__label
				top: 3px
				font-size: 10px
		&:hover
			border: 1px solid rgba(0, 0, 0, .4)
		&.has-error
			border-color: #f00
			box-shadow: 0 0 12px rgba(255,109,108,0.4)

	&__label
		position: absolute
		top: 15px
		left: 21px
		font-size: 16px
		user-select: none
		pointer-events: none
		+rl
		+tr(.4s)
		white-space: nowrap
		text-overflow: ellipsis
		max-width: 100%
	&__desc
		display: inline-block
		font-size: 16px
		margin-bottom: 0
		color: #000
		user-select: none
		+rl
	&__textarea
		width: 100%
		height: 100px
		resize: none
		padding: 20px
		border: 1px solid #cfc4b6
		background-color: #FFF
		+tr(.4s)
		+placeholder
			font-size: 16px
			color: #000
			+rl
		&:focus
			box-shadow: 0 0 12px rgba(131,109,108,0.4)
			border: 1px solid #cfc4b6
			outline: none
		&:hover
			border: 1px solid rgba(0, 0, 0, .4)
	&__agree
		text-align: center
	&__choose
		display: inline-block
		vertical-align: top

	.select
		height: 50px
		&:before
			right: 20px
			+s(icon-dubl)
		&.is-active
			z-index: 600
		&__text
			height: 50px
			line-height: 50px
			font-size: 16px
			text-transform: none
			+rl

		&__list
			top: 48px
			left: -1px
			right: -1px
			border: 1px solid #cfc4b6
			li
				padding: 0 20px
				text-transform: none
				text-align: left

.ui-widget.ui-widget-content
	+r(650)
		max-width: 280px

// ---------==================-------------
.radio
	position: relative
	display: inline-block
	margin: 0 0 0 20px
	+r(650)
		margin: 5px 20px 0 0
	input
		position: absolute
		left: 0
		top: 0
		z-index: 2
		border: 2px solid red
		opacity: 0
		&:checked
			+ span
				user-select: none
				&:after
					content: ""
					position: absolute
					top: 5px
					left: 5px
					width: 12px
					height: 12px
					border-radius: 100%
					background-color: #c7b299
		&:disabled
			+ span
				opacity: 1
	span
		position: relative
		padding-left: 28px
		left: 0
		top: 0
		font-size: 16px
		+rl
		&:before
			content: ""
			position: absolute
			top: 0
			left: 0
			width: 22px
			height: 22px
			border: 1px solid #c7b299
			background-color: #FFF
			border-radius: 100%
	// input + .checkbox
	// 	margin: 8px 20px 8px 0
	// 	input
	// 		&:checked
	// 			+ span
	// 				color: #000
	// 				font-weight: normal

.check
	position: relative
	display: inline-block
	input
		position: absolute
		top: 0
		left: 0
		z-index: 2
		opacity: 0
		&:checked
			+ span
				user-select: none
				&:after
					content: ""
					position: absolute
					bottom: 4px
					left: 1px
					+s(icon-checked)
		&:disabled
			+ span
				opacity: 1
	span
		position: relative
		padding-left: 28px
		left: 0
		top: 0
		font-size: 16px
		+rl
		&:before
			content: ""
			position: absolute
			top: 0
			left: 0
			width: 18px
			height: 18px
			border: 1px solid #c7b299
			background-color: #FFF
	.has-error + span
		color: #f00

.collage
	position: relative
	width: calc(100% / 3 - 1px)
	margin-right: 1px
	height: 426px //(292 + 134)
	background-color: #eee8e0
	&__pic
		position: relative
		height: 292px
		img
			+vcenter
			min-height: 100%
			min-width: 100%
	&__content
	&__head
		+clr
	&__color
		position: relative
		width: 50px
		height: 50px
		border-radius: 100%
		img
			+vcenter
			min-width: 100%
			min-height: 100%